.round {
    margin: 0;
    padding: 0;
    display: inline-block;
    width: 15px;
    height: 15px;
    background: #3498DB;
    border-radius: 10px;
    cursor: pointer;
  }
  
  .counter {
    display: inline-block;
    width: 40px;
    height: 15px;
  }
  
  .inner {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .toLeft{
      display: inline-block;
      float: right;
  }
  
  #classVariants {
      padding-top: 5px;
  }